* {
/* margin resets */
margin: 0;
padding: 0;
} 

html {
	scroll-behavior: smooth;
/* color palette */

	--Azul-Marino: lightblue;
	--Naraja-Palette: #f79218;
	--Amarillo-Pallete: #fbd15a;
	--Cafe-Pallete: #884104;
	--Cafelight-Pallete: #995253;
	--Marron-Pallete: #cab8b8;
	--Grey-Pallete: #F9F9F9;
	--Blanco-Pallete: #ffffff;
	--NegroTransparent-Pallete:rgba(0, 0, 0, 0.5);

	--Font1: "Times New Roman", Times, serif;
	--Font2: "Arial", Helvetica, sans-serif;
}

.navBar {
	
	padding: 10px 20px;
	/* background-color: lightblue; */
	background-color: var(--Amarillo-Pallete);
	position: fixed;
	width: 100%;
	height: 80px;
	-webkit-backdrop-filter: blur(10px);
	backdrop-filter: blur(10px);
	box-shadow: 0px 10px 13px -7px #000000, 0px 9px 13px 0px rgba(0,0,0,0.1);
	z-index: 1000;
}

.MenuOption {
	display: inline-block; 
	list-style: none;
	padding: 10px 20px;
float: right;  /*float to right */
margin-right: 5%;
margin-top: 1%; 
/* font-family: "Times New Roman", Times, serif; */
font-family: var(--Font2);
font-size: 25px;

}


.themeLogo  {
	font-style: italic;
	list-style: none;
	display: inline-block; 
	list-style: none;
	padding: 10px 20px;
	margin-top: 1%;
}


.burgerButton {
	display: none;
}
.offcanvasContainer {
		dispay: none;
}
.offcanvas {
	display: none;
}


/* Movil */
@media (max-width: 575.98px)and (orientation:portrait) {
	.MenuOption {
	font-size: 0px;
}
.themeLogo  {
	font-style: italic;
	list-style: none;
	display: inline-block; 
	list-style: none;
	padding: 15px 40px;
	margin-top: 1%;
}

.burgerButton {
	display: block;
	border-color: var(--naraja-palette);
	background-color: var(--Azul-marino);
	border-style: none;
}

.offcanvasContainer {
	background-color: var(--Naraja-Palette);
	font-family: var(--Font2);
	font-size: 25px;
	display: grid;
  	justify-content: center;
  	align-items: center;
  	height: 100vh; 
}

.offcanvas {
	display: block;
	border-style: none;
	width: 100%;
}


.offcanvasClose {
	background-color: var(--Naraja-Palette);
	border-style: none;
	width: 100%;
padding-left: 60%;
padding-top: 5px;

}

}

