/* Footer css */

.footerContainer {
	background-color: var(--Naraja-Palette);
	width: 100%;
	height: 10rem;
	}

.authorText {
	margin-top: 1rem;
	text-shadow: 1px 1px 1px var(--Blanco-Pallete);
}


.codedText {
	padding-top: 1rem;
	margin-bottom: 0.5rem;
	text-shadow: 1px 1px 1px var(--Blanco-Pallete);
 }


.menuOption {
		display: inline-block; 
		list-style: none;
		padding: 1rem  2rem;
		margin-right: 2rem;
		float: right;  /*float to right */
		font-family: var(--Font2);
		font-size: 25px;
		text-shadow: 1px 1px 1px var(--Blanco-Pallete);
}


/* --- MOVIL ---- */
@media (max-width: 575.98px)and (orientation:portrait) {
.footerContainer {
	background-color: var(--Naraja-Palette);
	width: 25rem;
	height: 15rem;
	position: relative;
	top: 80px; left: 1px;
	
	
}
.authorText {
	margin-top: 1rem;
	text-shadow: 1px 1px 1px var(--Blanco-Pallete)
}



.codedText {
	padding-top: 2rem;
text-shadow: 1px 1px 1px var(--Blanco-Pallete);
 }
.menuOption {
		display: inline-block; 
		list-style: none;
		padding: 1rem  0rem;
		margin-right: 1rem;
		float: right;  /*float to right */
		font-family: var(--Font2);
		font-size: 25px;
		text-shadow: 1px 1px 1px var(--Blanco-Pallete);
		
}




}



