.heroContainer {
	width: 100%;

}

.heroImage {
	margin-top: 5rem; ;
	width: 100%;
	height: 40rem;
}

.heroBlock {
	margin:10px;
	width: 35rem;
	height: 30rem;
	position: absolute;
	top: 10rem;
	left: 1rem;
	background-color: var(--NegroTransparent-Pallete);
	}

.heroText1 {
	color: var(--Blanco-Pallete);
	font-size: 4rem;
	margin: 1rem;
	padding: 1rem;
	margin-left: 3rem;
}

.customHr {
	width: 10rem;
	height: 1rem;
	background-color: var(--Naraja-Palette);
	border: var(--Naraja-Palette);
	margin-left: 7rem;
}

.customHrSections {
	width: 10rem;
	height: 0.5rem;
	background-color: var(--Naraja-Palette);
	border: var(--Naraja-Palette);
	margin-left: 7rem;
	margin:0 auto;
	margin-top: 1rem;

}

.heroText2 {
	color: var(--Blanco-Pallete);
	font-size: 2rem;
	margin: 1rem;
	padding: 1rem;
	margin-left: 3rem;
	margin-top: 2rem;
	font-family: var(--Font2);
}

/* SERVICES */
.servicesSectionMain{
	background-color: var(--Grey-Pallete);
}

.serviceImg {
	width: 25rem;
	height: 25rem;
	border-radius: 10%;
}


.servicesContainer {
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;

}
.servicesTitle{
	text-align: center;
	font-size: 35px;

}

.servicesCard {
	margin-top: 2rem;
	margin-bottom: 1rem;
}

.servicesCardText {
		margin-top: 0.5rem;
}

/* ABOUT */

.aboutImg {
	width: 60rem;
	height: 50rem;
	
}
.aboutContainer {
	display: flex; 
	margin-top:2rem;
	margin-bottom: 2rem;
}

.aboutImgContainer {
	width: 70%;
}
.aboutTextContainer {
	margin-top:2rem;
	margin-left:1rem;
	margin-right:1rem;
	width: 50rem;
}
.aboutTextTitle{
	font-size: 35px;
}
.aboutTextBlock{
	display:grid;
	margin-top: 2rem;
	text-align:justify;
	text-shadow: 1px 1px 1px var(--Naraja-Palette);
	font-size: 25px;
}

/* CONTACT */
.contactSectionMain{
	background-color: var(--Grey-Pallete);
	height:70rem;
}
.mapFormTitle {
	margin-left: 2rem;
	;
}

.mapContainer {
	display: flex;
	align-items: center;
	justify-content: center; 

}
.mapStyling {
	margin-top:2rem;
	width: 80%;
	height: 40rem;
	border: 0px;
}

.mapForm {
	background-color: var(--Blanco-Pallete);
	width: 70%;
	height: 35rem; 
	border: 0px;
	border-radius: 25px;

	position: relative;
	top: -50rem;
	left: 13rem;
margin-bottom: 2rem;
}

.mapFormBlock {
	display: flex;
	align-items: center;
	justify-content: center; 
	padding-top: 2rem;
}
.mapFormInput {
	width: 400px;
	height: 4rem;
	margin-radius: 25px;
	margin-top:1rem;
	margin-left:2rem;
	font-size: 2rem;
	border-style:solid;
	border-color:var(--Cafelight-Pallete);
	border-radius: 10px;
}

.mapFormInputEmail {
	width: 835px;
	height: 4rem;
	margin-radius: 25px;
	margin-top:1rem;
	margin-left:2rem;
	font-size: 2rem;
	border-style:solid;
	border-color:var(--Cafelight-Pallete);
	border-radius: 10px;
}
.mapFormInputMessage {
	width: 835px;
	margin-left:2rem;
	height: 7rem;
	font-size: 2rem;
	border-style:solid;
	border-color:var(--Cafelight-Pallete);
	border-radius: 10px;
}

.mapFormButton {
	width: 835px;
	height: 4rem;
	margin-top:3rem;
	margin-left:2rem;
	font-size: 2rem;
	border-style: none;
	background-color: var(--Amarillo-Pallete);
}


/* ------ Movil ------- */
